import React, { Component } from 'react'
import { Link } from 'react-router-dom';
import axios from 'axios';

import Unicode from './fontConverter/Unicode';
import Zawgyi from './fontConverter/Zawgyi';


export class View extends Component {
    state = {
        post:{},

    }

    componentDidMount(){
        let id = this.props.match.params.id;
        axios.get(`/api/post/${id}`)
            .then(res => {
                this.setState({
                    post:res.data
                })
            })
            .catch(err => console.log(err))
    }
    
    delete = e => {
       
      if(window.confirm('Are You Sure?')){

        axios.delete(`/api/post/delete/${this.state.post._id}`)
          .then(res => {
            this.props.history.push('/');
          })
          .catch(err => console.log(err));
      }
      
    }

    converter = (rule,{title,author,body})=>{
        let reg;

      for(let i=0;i < rule.length;i++){
        let con = rule[i];
        let from = con.from;
        let to = con.to;

        reg = new RegExp(from,"g");
        //replace
        title = title.replace(reg,to);
        author =author.replace(reg,to);
        body = body.replace(reg,to);
      }
      this.setState({
       post:{
         title:title,
         author:author,
         body:body
       }
     
      })
     
      
    }

    change = e => {
      switch(e.target.value){
        case 'Unicode':
          this.converter(Zawgyi,this.state.post); 
        break;
        case 'Zawgyi':
          this.converter(Unicode,this.state.post)
        break;
        default :
      }

    }

  render() {
      let {post} = this.state;
      
    return (
      <div>
          <h1>{post.title}</h1>
          <h4>Author <span className="text-secondary">{post.author}</span></h4>
          <div className="clearfix">
               <Link to={`/post/edit/${post._id}`} className="btn btn-light float-left" onClick={this.edit}>Edit</Link>
               <select onInput={this.change}  className="form-control-sm ml-5">
                 <option>off</option>
                 <option>Unicode</option>
                 <option>Zawgyi</option>
               </select>
               <button className="btn btn-danger float-right" onClick={this.delete}>Delete</button>

          </div>
          <div dangerouslySetInnerHTML={{__html:post.body}}  >
              
          </div>
      </div>
    )
  }
}

export default View
