import React, { Component } from 'react'
import axios from 'axios';
import Items from './Items';

export class Index extends Component {
  state = {
    posts: []
  }

  componentDidMount(){
    axios.get('/api/post')
      .then(res => {
        this.setState({
          posts:res.data
        })
      })
  }

  render() {
    const { posts } = this.state;
    return (
      <div>
        <div className="row">
          {posts.map((post)=>(
            <Items className="list-group-item" key={post._id} post={post} />
          ))}
        </div>
      </div>
    )
  }
}

export default Index
